section{background-color:#879099}.gst-return-container{padding:1rem 2rem;background:linear-gradient(to right, #03536b, #02334f);font-family:"Segoe UI",sans-serif;color:#1d1d1f}.gst-return-container .main-heading{text-align:center;font-size:2.8rem;color:#fff;font-weight:700;position:relative}.gst-return-container section{margin-bottom:3rem;padding:2rem;background:linear-gradient(135deg, #879099, #39707b);border-radius:15px;box-shadow:0 10px 25px rgba(0,0,0,.06);transition:all .3s ease-in-out}.gst-return-container section:hover{transform:translateY(-5px)}.gst-return-container section h2{font-size:1.8rem;color:#051519;margin-bottom:1rem;position:relative}.gst-return-container section p{font-size:1.05rem;line-height:1.7;color:#333}.gst-return-container section ul{list-style:none;margin-top:1rem;padding-left:0}.gst-return-container section ul li{position:relative;padding-left:1.5rem;margin-bottom:.75rem;font-size:1.05rem}.gst-return-container section ul li::before{content:"✔";position:absolute;left:0;color:#00b8ff;font-weight:bold}.gst-return-container section table{width:100%;border-collapse:collapse;margin-top:1rem;font-size:.95rem}.gst-return-container section table th,.gst-return-container section table td{padding:.8rem 1rem;border:1px solid #ccc}.gst-return-container section table th{background:linear-gradient(to right, #004aad, #007aff);color:#fff;font-weight:600;text-align:center}.gst-return-container section table td{background-color:#fdfdfd;background:linear-gradient(135deg, #879099, #39707b)}.gst-return-container section table tr:nth-child(even) td{background-color:#f5f9ff}.gst-return-container section .faq h4{font-weight:600;margin-top:1.5rem;color:#333}.gst-return-container section .faq p{font-size:1rem;color:#555;margin-bottom:1rem}.gst-return-container .cta-section{background:linear-gradient(135deg, #31727b, #046d82);color:#fff;border-radius:15px;padding:3rem 2rem;text-align:center;box-shadow:0 8px 25px rgba(0,0,0,.1)}.gst-return-container .cta-section h2{font-size:2.2rem;margin-bottom:1rem;font-weight:bold}.gst-return-container .cta-section p{font-size:1.1rem;margin-bottom:2rem}.gst-return-container .cta-section .gst-form{display:flex;flex-direction:column;gap:1rem;max-width:500px;margin:0 auto}.gst-return-container .cta-section .gst-form input,.gst-return-container .cta-section .gst-form textarea{padding:.9rem 1rem;font-size:1rem;border:none;border-radius:8px;outline:none;color:#333}.gst-return-container .cta-section .gst-form textarea{resize:none}.gst-return-container .cta-section .gst-form button{padding:.9rem;font-size:1.1rem;background:#fff;color:#004aad;font-weight:bold;border:none;border-radius:8px;cursor:pointer;transition:background .3s ease}.gst-return-container .cta-section .gst-form button:hover{background:#e1ecff}@media(max-width: 768px){.gst-return-container .main-heading{font-size:2.2rem}.gst-return-container section{padding:1.5rem}.gst-return-container .cta-section{padding:2rem 1.5rem}}