@import"https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap";.about-container{padding:3rem 1.5rem;margin:auto;font-family:"Inter",sans-serif;background:#02334f;background-color:#879099}.about-container section{background:#39707b;border-radius:16px;padding:2rem;margin-bottom:2rem;box-shadow:0 8px 24px rgba(0,0,0,.08);transition:transform .3s ease}.about-container section:hover{transform:translateY(-4px)}.about-container h2,.about-container h3{font-weight:700;font-size:1.8rem;color:#fff;margin-bottom:1rem}.about-container ul{list-style:none;padding:0;margin-top:1rem}.about-container ul li{display:flex;align-items:flex-start;margin-bottom:.8rem;font-weight:500;color:#caa604;position:relative;padding-left:1.8rem}.about-container ul li::before{content:"✔";color:#f3f3f7;position:absolute;left:0;top:0;font-size:1.1rem}.about-container p{font-size:1rem;color:#030303;margin-bottom:1rem}@media(max-width: 768px){.about-container section{padding:1.5rem}.about-container h2,.about-container h3{font-size:1.5rem}.about-container ul li{font-size:.95rem}.about-container p{font-size:.95rem}}