
.Footer {
    background-color: #111; // dark background like GoDaddy
    color: #fff;
    padding: 3rem 2rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  
    .footer-container {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      gap: 2.5rem;
      margin-bottom: 3rem;
      border-bottom: 1px solid #333;
      padding-bottom: 2rem;
  
      .footer-section {
        h4 {
          color: #fff;
          font-size: 1.1rem;
          margin-bottom: 1rem;
          text-transform: uppercase;
          font-weight: 600;
        }
  
        ul {
          list-style: none;
          padding: 0;
  
          li {
            margin: 0.4rem 0;
            color: #aaa;
            font-size: 0.95rem;
            cursor: pointer;
            transition: color 0.3s ease;
  
            &:hover {
              color: #00ffcc;
            }
          }
        }
      }
    }
  
    .footer-bottom {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      padding: 1rem 0;
  
      .footer-logo-lang-currency {
        display: flex;
        align-items: center;
        gap: 2rem;
        font-size: 0.95rem;
  
        .footer-logo {
          font-size: 1.4rem;
          font-weight: bold;
          color: #00ffcc;
        }
  
        .language-select,
        .currency-select {
          background-color: #1c1c1c;
          padding: 0.5rem 1rem;
          border-radius: 20px;
          cursor: pointer;
          transition: background-color 0.3s ease;
  
          &:hover {
            background-color: #333;
          }
        }
      }
  
      .social-icons {
        display: flex;
        gap: 1rem;
        font-size: 1.3rem;
        margin-top: 1rem;
  
        a {
          background-color: #060606;
          border-radius:50%;
          padding: 0.6rem;
          transition: all 0.3s ease;
          cursor: pointer;
          color: #fff; // default text color
        
          &:hover {
            background-color: #797e7d;
            color: #111;
          }
        
          &:nth-child(1) {
            background-color: rgb(76, 0, 255);
          }
        
          &:nth-child(2) {
            background-color: #97a7b8; // blue
          }
        
          &:nth-child(3) {
            background-color: #d53509; // green
          }
        
          &:nth-child(4) {
            background-color:rgb(0, 119, 181) ;// yellow
            color: #000; // contrast for yellow bg
          }
        
          &:nth-child(5) {
            background-color: #f51b76; // purple
          }
        }
        
      }
    }
  
    .footer-legal {
      text-align: center;
      font-size: 0.85rem;
      color: #999;
      padding-top: 2rem;
      border-top: 1px solid #333;
  
      p {
        margin-bottom: 0.5rem;
        line-height: 1.4;
      }
  
      .legal-links {
        span {
          margin: 0 0.6rem;
          cursor: pointer;
          transition: color 0.3s ease;
  
          &:hover {
            color: #00ffcc;
          }
        }
      }
    }
  
    @media (max-width: 768px) {
      .footer-logo-lang-currency {
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;
      }
  
      .footer-bottom {
        flex-direction: column;
        align-items: flex-start;
      }
    }
  }
  